import { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { hot } from 'react-hot-loader';
import { DefaultLayout } from '../layouts';
import './TermsPage.scoped.scss';


import { toast } from 'react-toastify';
import Number from 'react-number-format';
import { useModal } from 'react-simple-modal-provider';
import { useEagerConnect, useActiveWeb3React } from '../hooks/index';
import useAuth from '../hooks/useAuth';

import { Button, Header, Icon, Segment, Tab, Grid, Statistic } from 'semantic-ui-react';
import { CommonButton } from '../components';

import { utils } from 'ethers';
import * as A from '../store/actions';
import * as S from '../store/selectors';

import getNftContract from '../hooks/getNftContract';
import getGameContract from '../hooks/getGameContract';
import getGameAddress from '../hooks/getGameAddress';


const LOOT_TO_HIRE_1MOBSTER = 8640000;





const panes = [
  { menuItem: 'GAME', render: () => <Tab.Pane>Tab 1 Content</Tab.Pane> },
  { menuItem: 'REF', render: () => <Tab.Pane>Tab 3 Content</Tab.Pane> },
  { menuItem: 'NFT', render: () => <Tab.Pane>Tab 2 Content</Tab.Pane> },
]
const TabExampleBasic = () => <Tab menu={{ attached: false }} panes={panes} />



function GamePage() {
  const dispatch = useDispatch();

  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { library, chainId, account, active } = context;

  const { open: openWalletConnectModal } = useModal('WalletConnectModal');
  const accountShort = 'Connect wallet';
  const connectWallet = (e) => {
    e.preventDefault();
    openWalletConnectModal();
  }


  const parentAddress = useSelector(S.ref.getParentAddress);

  const blockNumber = useSelector(S.provider.getBlockNumber);
  const [isUpdating, setUpdating] = useState(false);

  const [balanceEthGame, setBalanceEthGame] = useState(0.00);

  const [balanceEthUser, setBalanceEthUser] = useState(0.00);
  const [userRewardsEth, setRewardsEth] = useState(0.00);
  const [userExtraRate, setUserExtraRate] = useState(0);
  const [userData, setUserData] = useState({
    lastClaim: 0,
    loot: '0',
    mobsters: '0',
    totalDeposit: 0.0,
    totalReinvest: 0.0,
    totalRefIncome: 0.0,
    totalRefs: 0,
    user: '0x...',
  });

  const [userTokens, setUserTokens] = useState([]);
  const [userTokensStaked, setUserTokensStaked] = useState([]);

  const [buyLootValue, setBuyLootValue] = useState(0);
  const onChangeBuyLootValue = async (e) => {
    e.preventDefault();
    setBuyLootValue(parseFloat(e.target.value));
    try {
      const constractGame = getGameContract(chainId, library);
      if (constractGame) {
        /*const value = utils.parseEther((buyLootValue).toString());
        console.log('calculateLootBuy value', value);
        const res = await constractGame.calculateLootBuy(value);
        console.log('calculateLootBuy res', res);
        console.log('calculateLootBuy res', res);
        .then((res) => {
           console.log('calculateLootBuy res', res);
         }).catch((error) => {
           console.error('calculateLootBuy error', error);
         });*/
      }
    } catch (error) {
      console.error(error);
      //error.message
    }
  }

  const onBlockHandler = async () => {
    if (isUpdating) return;
    setUpdating(true);

    dispatch(A.provider.setBlockNumber(blockNumber));
    try {
      let balanceEthGame = await library.getBalance(getGameAddress(chainId));
      balanceEthGame = utils.formatEther(balanceEthGame.toString());
      balanceEthGame = parseFloat(parseFloat(balanceEthGame).toFixed(4));
      setBalanceEthGame(balanceEthGame);


      let balanceEthUser = await library.getBalance(account);
      balanceEthUser = utils.formatEther(balanceEthUser.toString());
      balanceEthUser = parseFloat(parseFloat(balanceEthUser).toFixed(9));
      setBalanceEthUser(balanceEthUser);

      const constractGame = getGameContract(chainId, library);
      if (constractGame) {
        //const initialized = await constractGame.initialized();
        //console.log('initialized', initialized);

        const userDataOrig = await constractGame.users(account);
        let userData = {
          lastClaim: userDataOrig.lastClaim.toNumber(),
          loot: userDataOrig.loot.toString(),
          mobsters: userDataOrig.mobsters.toString(),
          totalDeposit: userDataOrig.totalDeposit.toString(),
          totalReinvest: userDataOrig.totalReinvest.toString(),
          totalRefIncome: userDataOrig.totalRefIncome.toString(),
          totalRefs: userDataOrig.totalRefs.toNumber(),
          user: userDataOrig.user,
        };
        userData.totalDeposit = utils.formatEther(userData.totalDeposit);
        userData.totalDeposit = parseFloat(parseFloat(userData.totalDeposit).toFixed(9));

        userData.totalReinvest = utils.formatEther(userData.totalReinvest);
        userData.totalReinvest = parseFloat(parseFloat(userData.totalReinvest).toFixed(9));

        userData.totalRefIncome = utils.formatEther(userData.totalRefIncome);
        userData.totalRefIncome = parseFloat(parseFloat(userData.totalRefIncome).toFixed(9));

        const loot = await constractGame.getMyLoot(account);
        userData.loot = loot.toString();
        setUserData(userData);

        let userRewardsEth = await constractGame.getMyRewards(account);
        userRewardsEth = utils.formatEther(userRewardsEth.toString());
        userRewardsEth = parseFloat(parseFloat(userRewardsEth).toFixed(9));
        setRewardsEth(userRewardsEth);
      }

      const constractNft = getNftContract(chainId, library);
      if (constractNft) {
        const userExtraRate = await constractNft.extraRate(account);
        setUserExtraRate(userExtraRate.toNumber());

        const userTokensOrig = await constractNft.tokensOfOwner(account);
        const userTokens = [];
        for (let i in userTokensOrig) {
          userTokens.push(userTokensOrig[i].toNumber());
        }
        setUserTokens(userTokens);

        const userTokensStakedOrig = await constractNft.tokensStakedOfOwner(account);
        const userTokensStaked = [];
        for (let i in userTokensStakedOrig) {
          userTokensStaked.push(userTokensStakedOrig[i].toNumber());
        }
        setUserTokensStaked(userTokensStaked);
      }

    } catch (error) {
      console.error(error);
      //dispatch(A.nft.clear());
    }
    setUpdating(false);
  }

  useEffect(() => {
    onBlockHandler();
  }, [blockNumber]);



  const onBuy = async () => {
    try {
      const constractGame = getGameContract(chainId, library, true);
      if (constractGame) {
        const value = utils.parseEther((buyLootValue).toString());
        let gasPrice = await library.getGasPrice();
        gasPrice = gasPrice.mul(120).div(100);
        let gasLimit;

        gasLimit = await constractGame.estimateGas.buyLoot(
          parentAddress,
          { value, gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);

        const tx = await constractGame.buyLoot(
          parentAddress,
          { value, gasPrice, gasLimit, }
        );
        toast('TX is Pending.');
        await tx.wait(1);
        toast('TX is Confirmed.');
      }
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }
  const onHire = async () => {
    try {
      const constractGame = getGameContract(chainId, library, true);
      if (constractGame) {
        let gasPrice = await library.getGasPrice();
        gasPrice = gasPrice.mul(120).div(100);
        let gasLimit;

        gasLimit = await constractGame.estimateGas.hireMobsters(
          { gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);

        const tx = await constractGame.hireMobsters(
          { gasPrice, gasLimit, }
        );
        toast('TX is Pending.');
        await tx.wait(1);
        toast('TX is Confirmed.');
      }
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }
  const onSell = async () => {
    try {
      const constractGame = getGameContract(chainId, library, true);
      if (constractGame) {
        let gasPrice = await library.getGasPrice();
        gasPrice = gasPrice.mul(120).div(100);
        let gasLimit;

        gasLimit = await constractGame.estimateGas.sellLoot(
          { gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);

        const tx = await constractGame.sellLoot(
          { gasPrice, gasLimit, }
        );
        toast('TX is Pending.');
        await tx.wait(1);
        toast('TX is Confirmed.');
      }
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }

  const onStakeAll = async () => {
    try {
      const constractNft = getNftContract(chainId, library, true);
      if (constractNft) {
        let gasPrice = await library.getGasPrice();
        gasPrice = gasPrice.mul(120).div(100);
        let gasLimit;

        gasLimit = await constractNft.estimateGas.stake(
          userTokens,
          { gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);

        const tx = await constractNft.stake(
          userTokens,
          { gasPrice, gasLimit, }
        );
        toast('TX is Pending.');
        await tx.wait(1);
        toast('TX is Confirmed.');
      }
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }

  const onUnstakeAll = async () => {
    try {
      const constractNft = getNftContract(chainId, library, true);
      if (constractNft) {
        let gasPrice = await library.getGasPrice();
        gasPrice = gasPrice.mul(120).div(100);
        let gasLimit;

        gasLimit = await constractNft.estimateGas.unstake(
          userTokensStaked,
          { gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);

        const tx = await constractNft.unstake(
          userTokensStaked,
          { gasPrice, gasLimit, }
        );
        toast('TX is Pending.');
        await tx.wait(1);
        toast('TX is Confirmed.');
      }
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }

  if (!triedEager || !library || !active)
    return (
      <DefaultLayout title='GAME'>
        <main className="main" style={{ minHeight: '95vh', }}>
          <section className="section section-terms">
            <div className="container">
              <Segment
                textAlign='center'
                style={{
                  background: 'rgba(255, 255, 255, 0.2)',
                  borderRadius: '24px',
                  minHeight: '320px',
                }}
              >
                <br />
                <Header as='h1' inverted>Wallet not connected</Header>
                <br />
                <br />
                <div>
                  <CommonButton variant="mod" onClick={connectWallet}>{accountShort}</CommonButton>
                </div>
              </Segment>
            </div>
          </section>
        </main>
      </DefaultLayout>
    );


  const timeBefore = (userData.lastClaim + 7 * 24 * 60 * 60) <= parseInt(Date.now() / 1000)
    ? 0
    : (userData.lastClaim + 7 * 24 * 60 * 60) - parseInt(Date.now() / 1000);
  //const timeBefore = parseInt((Date.now() + 7 * 24 * 60 * 60) / 1000 - userData.lastClaim);
  /*const timeBeforeD = parseInt(timeBefore / (24 * 60 * 60));
  const timeBeforeH = parseInt(timeBefore / (24 * 60 * 60));
  const timeBeforeM = parseInt(timeBefore / (24 * 60 * 60));
  const timeBeforeS = parseInt(timeBefore % 60);*/

  return (
    <DefaultLayout title='GAME'>
      <main className="main" style={{ minHeight: '95vh', }}>
        <section className="section section-terms">
          <div className="container">
            <Segment
              textAlign='center'
              style={{
                background: 'rgba(0, 0, 0, 0.33)',
                borderRadius: '24px',
                minHeight: '320px',
              }}
            >
              <Grid columns={3} stackable>
                <Grid.Row>
                  <Grid.Column>
                    <Statistic label='My Mobsters' value={userData.mobsters} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='My Loot' value={`${userData.loot} (${userRewardsEth} ETH)`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Contract balance' value={`${balanceEthGame} ETH`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    {/*<Statistic label='Time before' value={`${timeBefore} sec`} inverted />*/}
                    <Statistic label='Time before' value={`1d 15h 15m 15s`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Total Deposited' value={`${userData.totalDeposit} ETH`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Total Reinvested' value={`${userData.totalReinvest} ETH`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Total Ref Income' value={`${userData.totalRefIncome} ETH`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Total Refs' value={`${userData.totalRefs}`} inverted />
                  </Grid.Column>
                </Grid.Row>
              </Grid>
              <div>
                <input
                  id="buyLootValue"
                  name="buyLootValue"
                  type="number"
                  min={0}
                  value={buyLootValue}
                  onChange={onChangeBuyLootValue}
                  style={{ backgroundColor: 'white', color: 'black', textAlign: 'center', }}
                /> ETH
                <br />
                <br />
                <Button size='huge' onClick={onBuy}>Buy Loot</Button>
                <Button size='huge' onClick={onHire}>Hire Mobsters</Button>
                <Button size='huge' onClick={onSell}>Sell Loot</Button>
              </div>
              <br />

              <Grid columns={3} stackable>
                <Grid.Row>
                  <Grid.Column>
                    <Statistic label='Balance NFT' value={`${userTokens.length} NFT`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Staked NFT' value={`${userTokensStaked.length} NFT`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Extra rate' value={`${userExtraRate / 100} %`} inverted />
                  </Grid.Column>
                </Grid.Row>
              </Grid>

              <div>
                <Button size='huge' onClick={onStakeAll}>Stake All</Button>
                <Button size='huge' onClick={onUnstakeAll}>Unstake All</Button>
              </div>
              <br />
            </Segment>
          </div>
        </section>
      </main>
    </DefaultLayout>
  );
};

export default hot(module)(GamePage);
